
.questionAnsContainerRadio {
  border-bottom:  solid 0.3px black;
  
}


.radio_container{
  background-color: #FFFBFA;
  border-left: solid 0.2px black ;
  border-right: solid 0.2px black;
  border-top: solid 0.2px black;
  padding: 5px;
   font-family: 'Poppins';
  color: black;
  
}

.radio_container input:checked+span {
  background-color: inset 0 0 0 0.4375em #F6D4C9;
}



.radio_label input {
  position: absolute;
  left: -9999px;
}

.radio_label input:checked+span {
  background-color: transparent
}

.radio_label input:checked+span:before {
  box-shadow: inset 0 0 0 0.4375em #F6D4C9;
}

.radio_label span {
  display: flex;
  padding: 3px;
  transition: 0.25s ease;
}


.radio_label span:before {
  display: flex;
  flex-shrink: 0;
  content: "";
  background-color: #fff;
  width: 1.5em;
  height: 1.5em;
  border-radius: 50%;
  margin-right: 0.375em;
  transition: 0.25s ease;
  box-shadow: inset 0 0 0 0.125em black;
}

.radio_label input:checked+span:after{
  background-color: black;
}



